import React, { Component } from 'react'
import styles from './index.less'
import LoadingComponent from '../../components/LoadComponent';
import createDva from '../../command/createDva';
import apiTool from '../../command/apiTool';

@createDva(['video'])
export default class Video extends Component {

  componentDidMount() {
    apiTool.send(this,[
      {
        url:'/official/web/officialWeb/listVideo',
        method:'GET',
        target:'video'
      }
    ])
  }

  renderBkImage = () =>{
    return (
      <img src={require('../../images/video/bgc.png')} className={styles.bkImage}/>
    )
  }

  renderList = () =>{
    const {data} = this.props
    return (
      <div className={styles.listView}>
        {
          data.map((e,i)=>{
            return (
              <div key={i} className={styles.listItemView}>
                <img src={e.videoPic}/>
                <div className={styles.listItemFoot}>
                  <h4>{e.videoName}</h4>
                  <div style={{ color: '#999999', fontFamily:'PingFang-SC-Regular'}}>{e.remark}</div>
                  <div className={styles.listItemFootButton}>点击播放</div>
                </div>
              </div>
            )
          })
        }
      </div>
    )
  }

  renderFoot = () =>{
    const data = ['请拨打电话：0574-55843501/189 5741 9012','工作时间：法定工作日9：00-18：00']
    return (
      <div className={styles.footView}>
        <h4>如果依然无法解答您的疑问</h4>
        {
          data.map((e)=>{
            return (
              <div>{e}</div>
            )
          })
        }
      </div>
    )
  }

  renderView = () =>{
    return (
      <div style={{flex:1,display:'flex',flexDirection:'column'}}>
        {/* 渲染背景 */}
        {this.renderBkImage()}
        {/* 渲染列表 */}
        {this.renderList()}
        {/* 渲染底部说明 */}
        {this.renderFoot()}
      </div>
    )
  }

  render() {
    const {isShow} = this.props
    console.log('输出props',this.props)
    return (
      <div className={styles.main}>
        <LoadingComponent
          isShow={isShow}
          renderView={this.renderView}
        />
      </div>
    )
  }
}
